<template>
  <el-row class="container">
    <el-tabs :tab-position="tabPosition">
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label="">
        <el-tabs type="border-card">
          <el-tab-pane label="">
            <el-table class="order_container" :data="orderList">
              <el-table-column prop="orderNo" label="" width="180">
              </el-table-column>
              <el-table-column prop="name" label="">
              </el-table-column>
              <el-table-column prop="name" label="">
                <template>
                  <img :src="defaultUrl" alt="" height="100">
                </template>
              </el-table-column>
              <el-table-column prop="price" width="150" label="">
              </el-table-column>
              <el-table-column prop="count" label="">
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <span class="total">{{scope.row.count * scope.row.price}}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
          <el-tab-pane label=""></el-tab-pane>
        </el-tabs>
      </el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
    </el-tabs>
  </el-row>
</template>
<script>
export default {
  data () {
    return {
      order: ['', '', '', ''],
      orderList: [],
      defaultUrl: require('./default.jpg'),
      tabPosition: 'left'
    }
  },
  computed: {
    total () {
      return this.orderList.reduce((pre, cur) => {
        pre.total = pre.total + cur.total
        return pre
      }, { total: 0 }).total
    }
  },
  activated () {
    this.$http.get('/api/order/orderList', {
      params: {
        user: this.$route.params.user
      }
    }).then((response) => {
      const { status, data: { code, res } } = response
      if (status !== 200 || code !== 0) return
      this.orderList = res
      console.log(this.orderList)
    })
  }
}
</script>
<style scoped>
  .container {
    width: 80%;
    margin: 30px auto;
  }

  .tabs {
    text-align: left;
  }
</style>



